import Header from '@/components/header';
import { loginOut } from '@/lib/axios/api';

export default {
  data () {
    return {
      accountName: '',
    }
  },
  methods: {
    getAccount (data) {
      this.accountName = data;
    },
    userLoginOut () {
      loginOut('/SignIn/Logout')            // login
      .then((res) => {
        console.log('',res);
        if(res.status===204){
          this.$router.push('/login')
        } else {
          this.$Message.error('');
        }
      })
      .catch((err) => {
        console.log('',err);
        this.$Message.error('');
      })
    }
  },
  components: {
    headerNav: Header
  },
}



