import { fetch } from "./fetch"; //fetch.js
import api from './url'; //url.js

//
export function login(url,data) {
  return fetch({
    //api.baseUrl url.js
    url: api.baseUrl + url,
    method: 'post',//
    data:data
  })
};

//(home)
export function sysList(url,data) {
  return fetch({
    url: api.baseUrl + url,
    method: 'post',
    data:data
  })
};

