/**
 * VuelocalStorage
 **/
let local = {
  save (key, value) {
    localStorage.setItem(key, JSON.stringify(value))
  },
  fetch (key) {
    return JSON.parse(localStorage.getItem(key)) || {}
  },
  del (key) {
    localStorage.removeItem(key)
  }
}

/**
 * Vue
 **/
let regEn = /[`~!@#$%^&*()_+<>?:"{},.\/;'[\]]/im,          //
    regCn = /[#|[\]]/im,  //
    regZh = /[\u4e00-\u9fa5]/ig,                          //
    regCha = /[a-zA-Z]/ig,                                //
    regNum = /[0-9]/ig,                                   //

    publicMethod = {
      noSpecialCharacter (data) { // truefalse
        return regEn.test(data) || regCn.test(data) || regZh.test(data)
      },
      chineseOnly (data) { // truefalse
        return regCha.test(data) || regEn.test(data) || regCn.test(data)
      },
    }

export default {
  install: function (vm) {
    vm.prototype.$local = local;
    vm.prototype.$publicMethod = publicMethod
  }
}



